<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-0"
			:headers="header"
			:items="dataUnit"
			:items-per-page="10">		
			<template v-slot:item.no="{item}">
				{{dataUnit.map(x => x.id).indexOf(item.id)+1}}
			</template>
			<template v-slot:item.aktivasi="{ item }">
	      <v-btn 
	      	style="border-radius: 2px 0 0 2px"
	      	x-small
	      	@click="item.aktivasi = true"
	      	:color="item.aktivasi === true?'success':'default'"
	      	depressed>
	      	on
	      </v-btn>
	      <v-btn 
	      	style="border-radius: 0 2px 2px 0"
	      	x-small
	      	@click="item.aktivasi = false"
	      	:color="item.aktivasi === false?'red':'default'"      	
	      	:dark="item.aktivasi === false?true:false" 
	      	depressed>
	      	off
	      </v-btn>
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditUnit :data="item" />
				<HapusUnit :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import EditUnit from '@/components/Unit/EditUnit'
	import HapusUnit from '@/components/Unit/HapusUnit'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				// { text: 'Username', value: 'user', sortable: false },
				{ text: 'Password', value: 'password', sortable: false },
				{ text: 'Kepala Unit', value: 'nama_kepala', sortable: false },
				{ text: 'No HP/WA', value: 'nohp', sortable: false },
				{ text: 'Aktifasi', value: 'aktivasi', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					id: 1,
					name: 's',
					user: 's',
					pass: 's',
					kepala: 's',
					hp: 's',
					aktivasi: true
				}
			],
		}),

		components: {
			EditUnit,
			HapusUnit,
			Loader
		},

		created() {
			this.$store.dispatch('dataUnit/getDataUnit')
			this.$store.dispatch('dataUnit/updateIsLoading', true)
		},

		computed: {
			...mapState('dataUnit', ['isLoading', 'dataUnit'])
		}
	}
</script>