<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Unit
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataUnit.nama"
            label="Nama unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.nama_kepala"
            label="Kepala unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.nohp"
            label="No Hp/WA"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.alamat"
            label="Alamat kantor"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="dataUnit.provinsi_id"
            :items="dataProv"
            item-text="nama"
            item-value="id"
            label="Provinsi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-autocomplete
            v-model="dataUnit.kabupaten_id"
            :items="dataKab"              
            item-text="nama"
            item-value="id"
            :disabled="dataUnit.provinsi_id === ''?true:false"
            required
            label="Kabupaten"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>

          <Notif :status="status" :msg="msg" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataUnit: {
          nama: '',
          nama_kepala: '',
          nohp: '',
          alamat: '',
          provinsi_id: '',
          kabupaten_id: ''
        },
        item: ['foo', 'bar', 'zee'],
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambah',
          error: 'Data berhasil diedit',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataUnit', ['isLoading', 'status']),
      ...mapState('dataTempat', ['dataProv', 'dataKab'])
    },

    watch: {
      'dataUnit.provinsi_id': function (val) {
        this.$store.dispatch('dataTempat/getDataKab', val)
        this.$store.dispatch('dataTempat/updateIsLoading', true)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataUnit/postDataUnit', this.dataUnit)
          this.$store.dispatch('dataUnit/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataUnit/updateStatus', null)
      }
    }
  }
</script>
