<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        v-on="on"
      	icon 
      	color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Edit Unit
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="data.nama"
            label="Nama unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.password"
            label="Password"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.nama_kepala"
            label="Kepala unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.nohp"
            label="No Hp/WA"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="data.alamat"
            label="Alamat kantor"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="data.provinsi.id"
            :items="detailProv"
            item-text="nama"
            item-value="id"
            label="Provinsi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-autocomplete
            v-model="data.kabupaten.id"
            :items="detailKab"            
            item-text="nama"
            item-value="id"
            :disabled="data.provinsi.id === ''?true:false"
            label="Kabupaten"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>

          <Notif :status="status" :msg="msg" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
  	props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil diedit',
          error: 'Data gagal diedit',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: { 
      ...mapState('dataUnit', ['status', 'isLoading']), 
      ...mapState('dataTempat', ['dataProv', 'dataKab']),
      detailProv: {
        get: function () {
          return this.dataProv
        },
        set: function (val) {
          this.dataProv.filter(prov => prov.id === val)
        }
      },
      detailKab: {
        get: function () {
          return this.dataKab
        },
        set: function (val) {
          this.dataKab.filter(kab => kab.id === val)
        }
      },
    },

    watch: {
      dialog(val) {
        if (val === true) {
          this.$store.dispatch('dataTempat/getDataKab', this.data.provinsi.id)
        }
      },
      'data.provinsi.id': function (val) {
        this.$store.dispatch('dataTempat/getDataKab', val)        
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.data.provinsi_id = this.data.provinsi.id
          this.data.kabupaten_id = this.data.kabupaten.id
          this.$store.dispatch('dataUnit/putDataUnit', this.data)
          this.$store.dispatch('dataUnit/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
  }
</script>
